import "../style.css"
import { practiceClips, initWorkerId, TEXT } from '../../utils'
import { VideoQuestion, Panel, Modal } from '../../components'

initWorkerId()

// init panel
const instructionPanel = new Panel({
    containerId: 'instruction',
    title: TEXT.instruction.title,
    content: TEXT.instruction.content,
    sticky: true
})

const succeedModal = new Modal({
    containerId: 'succeedModal',
    title: TEXT.readyToTaskPanel.title,
    content: TEXT.readyToTaskPanel.content,
    expandContent: TEXT.readyToTaskPanel.expandContent,
    onClickButton: (evt, panelCmp) => {
        if (!panelCmp.hasBeenExpanded) {
            panelCmp.showExpandContent()
        } else {
            location.href = '/task.html'
        }
    },
    buttonTitle: 'Continue',
})

const failedModal = new Modal({
    containerId: 'failedModal',
    title: TEXT.failedQualificationPanel.title,
    content: TEXT.failedQualificationPanel.content,
    onClickButton: () => location.href = '//www.mturk.com/',
    buttonTitle: 'OK',
})

const videoQuestions = practiceClips.map((d, idx) => {
    const container = document.getElementById('rootContainer')
    container.insertAdjacentHTML('beforeend', `<div class="flex-child" id="vq_${d.id}"></div>`)

    const videoQuestion = new VideoQuestion({
        containerId: `vq_${d.id}`,
        clipId: d.id,
        clipIdx: idx,
        videoTitle: `Video ${idx + 1} / 10`,
        correctAns: d.ans,
    })

    // once play, pause others
    videoQuestion.onPlay(() => {
        videoQuestions
            .filter(v => v.clipId !== d.id)
            .forEach(v => v.pauseVideo())
    })

    return videoQuestion
})

/**
 * Add event handler to the submit button
 */
document.querySelector('#btn_submit')
    .addEventListener('click', () => {

        // check all answer
        // alert if some videos haven't been played
        if (videoQuestions.some(v => !v.hasPlayed)) {
            const videosNotPlay = videoQuestions.filter(v => !v.hasPlayed).map(v => `Video ${v.clipIdx + 1}`)
            alert(`You haven't watched these videos: ${videosNotPlay.join('\n')}`)
            return
        }

        // alert if some videos haven no answer
        if (videoQuestions.some(v => v.ans === null)) {
            const videosNotAnswer = videoQuestions.filter(v => !v.ans).map(v => `Video ${v.clipIdx + 1}`)
            alert(`You haven't answered these videos: ${videosNotAnswer.join('\n')}`)
            return
        }

        // show all answers
        const score = videoQuestions.reduce((sum, v) => sum + v.submitAns(), 0)
        console.log(score) // store particiapnt ans from form value

        //3. if correctAnswer >=9, proceed to actual task 
        const modalToShow = (score >= 9 && score <= 10) ? succeedModal : failedModal
        modalToShow.title = `${modalToShow.title} Score: ${score}/10`
        modalToShow.show()
    })